@import url('../../../../App.css');
.friend {
    display: block;
}
.avatar {
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 5px;
    box-shadow: 5px 5px 5px rgb(150, 150, 150);
    margin: 0 auto;
    object-fit:cover;
    border: 1px solid rgb(220, 220, 220);
    transition: .3s all;
    cursor: pointer;
}
.avatar:hover {
    transform: scale(1.02);
}
.avatar:active {
    transform: scale(.98);
}
.name {
    text-align: center;
    font-size: 12px;
    line-height: .9;
    margin-top: 5px;
    cursor: pointer;
}
.name:hover {
    transform: scale(1.02);
}
.name:active {
    transform: scale(.98);
}